import {Spin, Select, Option, Icon, Page, CheckboxGroup, RadioGroup, Radio, Checkbox} from 'view-design'

@Component({
    components: {
        Icon,
        Spin,
        Page,
        Select,
        Option,
        CheckboxGroup,
        RadioGroup,
        Radio,
        Checkbox,
    }
})


export default class orderList extends Vue {

    orderList: any = [
        {
            reporter: '张娜拉',
            time: '2020-5-3 19:32:32',
            morning: '调研',
            afternoon: '-',
        },
        {
            reporter: '张文',
            time: '2020-5-13 19:33:12',
            morning: '实地勘察',
            afternoon: '写报告',
        },
    ];
    orderList2: any = [
        {
            department: '信息部',
            time: '2020-5-3 19:32:32',
            lastweek: '调研',
            thisweek: '-',
            nowpush: '--',
        },
        {
            department: '人事部',
            time: '2020-5-13 19:33:12',
            lastweek: '调研',
            thisweek: '面试',
            nowpush: '筛选人员',
        },
    ];
    type: number = 1;

    formHeight: number = 600;
    pageNum: number = 1
    pageSize: number = 10
    totalElements: number = 20

    setPagesize() {
        let refs: any = this.$refs;
        let maxHeight = refs.viewbox.clientHeight;
        let searchline = refs.searchLine.clientHeight;
        let pageBottom = refs.pageBottom.clientHeight;
        this.formHeight = maxHeight - searchline - pageBottom;
        //表头高度49px，每一列高度48px;
        this.pageSize = Math.floor((this.formHeight - 49) / 48);
    }

    mounted(): void {
        this.$nextTick(() => {
            this.setPagesize();
        })
    }

    getPage(page?: number) {
        this.pageNum = page || 1;
        console.log(page)
    }
}

